.solution-slot {
  border-radius: 4px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2px;
  background-color: #dddddd;
}

@-webkit-keyframes cursor-blink {
  0% {
    opacity: 0;
  }
}

@keyframes cursor-blink {
  0% {
    opacity: 0;
  }
}

.active-solution-slot::after {
  content: "";
  width: 1px;
  height: 20px;
  background: black;
  margin-left: 0px;
  display: inline-block;
  -webkit-animation: cursor-blink 1.25s steps(2) infinite;
          animation: cursor-blink 1.25s steps(2) infinite;
}

.solution-slot-container {
  display: flex;
}

