.feedbackContainer {
  position: relative;
  height: 0;
  width: 0;
}

.incorrectFeedback {
  background-color: #25292e;
  border-radius: 8px;
  border-width: 0;
  font-size: 12px;
  height: 36px;
  justify-content: center;
  line-height: 1;
  padding: 0 8px;
  align-items: center;
  justify-content: center;
  display: inline-flex;
  color: white;
  opacity: 0;
  -webkit-transform: opacity;
          transform: opacity;
  transition: 0.2s;
  transition-timing-function: ease-in;
  font-size: 16px;
  text-transform: none;
  font-weight: normal;
  letter-spacing: normal;
  margin-left: 8px;
  position: absolute;
  right: -8px;
  bottom: 30px;
  width: -webkit-max-content;
  width: max-content;
}

.incorrectFeedback:after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: auto;
  right: 8px;
  bottom: -8px;
  border: 6px solid;
  border-color: #25292e #25292e transparent transparent;
}

.showIncorrectFeedback {
  opacity: 1;
}

@media screen and (max-width: 639px) {
  .incorrectFeedback {
    bottom: 24px;
  }
}

#tileArea {
  display: inline-flex;
  margin: 24px 0;
  position: relative;
}

.tile {
  height: 60px;
  width: 60px;
  display: inline-flex;
  margin: 4px;
  border: 1px solid #888888;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 24px;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.tile:active {
  -webkit-transform: scale(0.96);
          transform: scale(0.96);
}

.flip-container .flipper.flip {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.flipper {
  width: 100%;
  height: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: -webkit-transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.front,
.back {
  position: absolute;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);
}

.back {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

.emoji-tile:active {
  -webkit-transform: none;
          transform: none;
}

.active-solution-slot::after {
  content: "";
  width: 1px;
  height: 20px;
  background: black;
  margin-left: 0px;
  display: inline-block;
  -webkit-animation: cursor-blink 1.25s steps(2) infinite;
          animation: cursor-blink 1.25s steps(2) infinite;
}

.used-tile {
  color: white;
  opacity: 0.5;
  pointer-events: none;
}


